.backdrop {
  @extend %backdrop;
}
.modal {
  min-height: 400px;
  @extend %modal;
}
.modal-close {
  @extend %modal-close;
}
.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.modal__title {
  @include font(28px, 1.15, 0.04em, 400);
  @extend %modal-title;
  margin-bottom: 25px;
}
