.modal-form__comment {
  width: 100%;
  height: 80px;
  border-radius: 24px;
  border: 1px solid $accent-color;
  outline: none;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 10px;
  margin-bottom: 20px;
  color: $third-text-color;
  resize: none;
}
.modal-form__text {
  display: block;
  margin-bottom: 8px;
  @include font(18px, 1.15, 0.02em, 500);
  color: $second-text-color;
}

.modal-form_button {
  @extend %button;
  @include font(16px, 1.3, 0.04em, 400);
  display: block;
  margin: 0 auto;
  width: 200px;
  height: 40px;
  color: $btn-light-text-color;
  background-color: $btn-dark-text-color;
}

.modal-form__comment::placeholder {
  @include font(12px, 1.15, 0.02em, 400);
  color: $third-text-color;
  opacity: 0.5;
}
.group {
  padding-bottom: 20px;
}
.group-title {
  margin-bottom: 7px;
  @include font(18px, 1.15, 0.02em, 500);
  color: $second-text-color;
}
.product-options__item {
  &:not(:last-child) {  
    margin-bottom: 5px;
  }
    @include font(16px, 1.15, 0.02em, 500);
    color: $third-text-color;

}
.product-size,
.product-topping {
  display: flex;
}
.product-size__item, 
.product-topping__item {
  &:not(:last-child) {  
    margin-right: 15px;
  }
    @include font(16px, 1.15, 0.02em, 500);
    color: $third-text-color;
}

